import React from 'react'
import { 
    Edit, SimpleForm, DisabledInput, SelectInput,
    TextInput, ImageInput, ImageField
} from 'react-admin';
import { validateStaff } from '../../validators/simple.validator';

const StaffTitle = ({ staff }) => {
    return <span>Сотрудник {staff ? `"${staff.name}"` : ''}</span>;
};

export const StaffEdit = props => (
    <Edit title={<StaffTitle/>} {...props}>
        <SimpleForm validate={validateStaff}>
            <DisabledInput source="id"/>
            <TextInput label="Имя" source="name" />
            <TextInput label="Фамилия" source="lastName" />
            <TextInput label="Отчество" source="patronymic" />
            <TextInput label="Дата рождения" source="dateOfBirth" />
            <SelectInput required label="Дожность" source="positions" 
            choices={[
                { id: 'директор', name: 'директор' },
                { id: 'заместитель директора', name: 'заместитель директора' },
                { id: 'главный инженер', name: 'главный инженер'},
                { id: 'Начальник Бюро приёма и размещения', name: 'Начальник Бюро приёма и размещения'},
                { id: 'сотрудник Бюро приёма и размещения', name: 'сотрудник Бюро приёма и размещения'},
                { id: 'Заведующий торговым объектом общественного питания', name: 'Заведующий торговым объектом общественного питания'},
                { id: 'сотрудник общественного питания', name: 'сотрудник общественного питания'},
                { id: 'заведующий клининговой службы', name: 'заведующий клининговой службы'},
                { id: 'сотрудник клининговой службы', name: 'сотрудник клининговой службы'},
                { id: 'Начальник турбюро', name: 'Начальник турбюро'},
            ]} />
            <TextInput label="Зарплата" source="salary" />
        </SimpleForm>
    </Edit>
);