import React from 'react';
import { 
    List, Datagrid, TextField,  ImageField
} from 'react-admin';

export const ClientList = props => (
    <List {...props} bulkActionButtons={false} title='Пользователи'>
        <Datagrid rowClick="edit">
            <TextField source="id"/>
            <TextField label="Имя" source="name" />
            <TextField label="Фамилия" source="lastName" />
            <TextField label="Отчество" source="patronymic" />
            <TextField label="Дата рождения" source="dateOfBirth" />
        </Datagrid>
    </List>
);