import React from 'react'
import { 
    Create, SimpleForm, SelectInput, TextInput,
    ImageInput, ImageField,
} from 'react-admin';
import { validateClient } from '../../validators/simple.validator';

export const ClientCreate = props => (
    <Create {...props} title="Добавление клиента">
        <SimpleForm validate={validateClient}>
            <TextInput label="Имя" source="name" />
            <TextInput label="Фамилия" source="lastName" />
            <TextInput label="Отчество" source="patronymic" />
            <TextInput label="Дата рождения" source="dateOfBirth" />
        </SimpleForm>
    </Create>
);