import React from 'react'
import { 
    Create, SimpleForm, SelectInput, TextInput,
    ImageInput, ImageField,
} from 'react-admin';
import { validateStaff} from '../../validators/simple.validator';

export const StaffCreate = props => (
    <Create {...props} title="Добавление сотрудника">
        <SimpleForm validate={validateStaff}>
            <TextInput label="Имя" source="name" />
            <TextInput label="Фамилия" source="lastName" />
            <TextInput label="Отчество" source="patronymic" />
            <TextInput label="Дата рождения" source="dateOfBirth" />
            <TextInput label="Должность" source="position" />
            <TextInput label="Зарплата" source="salary" />
        </SimpleForm>
    </Create>
);