import React from 'react'
import { 
    Edit, SimpleForm, DisabledInput, SelectInput,
    TextInput, ImageInput, ImageField
} from 'react-admin';
import { validateClient } from '../../validators/simple.validator';

const ClientTitle = ({ client }) => {
    return <span>Пользователь {client ? `"${client.name}"` : ''}</span>;
};

export const ClientEdit = props => (
    <Edit title={<ClientTitle/>} {...props}>
        <SimpleForm validate={validateClient}>
            <DisabledInput source="id"/>
            <TextInput label="Имя" source="name" />
            <TextInput label="Фамилия" source="lastName" />
            <TextInput label="Отчество" source="patronymic" />
            <TextInput label="Дата рождения" source="dateOfBirth" />
        </SimpleForm>
    </Edit>
);