import React from 'react'
import { 
    Edit, SimpleForm, DisabledInput, DateInput,
    ReferenceInput, SelectInput, LongTextInput,
    TextInput, ImageInput, ImageField, required,
    AutocompleteInput
} from 'react-admin';
import { validateReservation } from '../../validators/simple.validator';

const ReservationTitle = ({ reservation }) => {
    return <span>Регистрация {reservation ? `"${reservation.id}"` : ''}</span>;
};

export const ReservationEdit = props => (
    <Edit title={<ReservationTitle/>} {...props}>
           <SimpleForm validate={validateReservation}>
        <DateInput required label="Дата заселения" source="check_in" />
        <DateInput required label="Дата выселения" source="check_out" />
        <ReferenceInput label="Клиент" validate={required('Выберите клиента!')} source="client.lastName" reference="client">
            {/* <SelectInput optionText="lastName"/> */}
            <AutocompleteInput source="lastName"/>
            {/* AutocompleteInput source="category" */}
        </ReferenceInput>
        <ReferenceInput label="Комната" validate={required('Выберите комнату!')} source="rooms.id" reference="rooms">
            {/* <SelectInput optionText="id"/> */}
            <AutocompleteInput source="id"/>
            {/* AutocompleteInput source="category" */}
        </ReferenceInput>
        <ReferenceInput validate={required('Выберите сотрудника!')} label="Сотрудник" source="staff.lastName" reference="staff">
            <SelectInput optionText="lastName"/>
        </ReferenceInput>
    </SimpleForm>
    </Edit>
);