import React from 'react'
import { 
    Edit, SimpleForm, DisabledInput, TextInput
 } from 'react-admin';
import { validateСategoryRoom } from '../../validators/simple.validator'

const CategoryRoomTitle = ({ categoryRoom }) => {
    return <span>Категория комнат {categoryRoom ? `"${categoryRoom.name}"` : ''}</span>;
};

export const CategoryRoomEdit = props => (
    <Edit title="Изменение катеогрии комнат" {...props}>
        <SimpleForm validate={validateСategoryRoom}>
            <TextInput label="Название" source="name"/>
            <TextInput label="Количество комнат" source="numberOfRooms"/>
            <TextInput label="Количесвто мест" source="numberOfSeats"/>
            <TextInput label="Стоимость в сутки" source="cost_per_day"/>
        </SimpleForm>
    </Edit>
);