import React from 'react'
import { 
    Create, SimpleForm, SelectInput, TextInput,
    ImageInput, ImageField,
} from 'react-admin';
import { validateUser } from '../../validators/simple.validator';

export const UserCreate = props => (
    <Create {...props} title="Добавление пользователя">
        <SimpleForm validate={validateUser}>
            <TextInput required label="Логин" source="login"/>
            <TextInput required label="Пароль" type='password' source="password" />
            <SelectInput required label="Роль" source="role" 
                choices={[
                    { id: 'USER', name: 'USER' },
                    { id: 'ADMIN', name: 'ADMIN' },
                    { id: 'STAFF', name: 'STAFF'},
                ]}
                require
            />
        </SimpleForm>
    </Create>
);