import React from 'react'
import { 
    Edit, SimpleForm, DisabledInput, SelectInput,
    TextInput, ImageInput, ImageField
} from 'react-admin';
import { validateUser } from '../../validators/simple.validator';

const UserTitle = ({ user }) => {
    return <span>Пользователь {user ? `"${user.login}"` : ''}</span>;
};

export const UserEdit = props => (
    <Edit title={<UserTitle/>} {...props}>
        <SimpleForm validate={validateUser}>
            <DisabledInput source="id"/>
            <TextInput required label="Логин" source="login" />
            <TextInput required label="Пароль" type='password' source="password" />
            <SelectInput required label="Роль" source="role" 
            choices={[
                { id: 'USER', name: 'USER' },
                { id: 'ADMIN', name: 'ADMIN' },
                { id: 'STAFF', name: 'STAFF'},
            ]}
            />
        </SimpleForm>
    </Edit>
);