import React from 'react';
import { 
    Create, SimpleForm, ReferenceInput,
    SelectInput,
    ImageInput, ImageField,required 
} from 'react-admin';
import { validateRoom } from '../../validators/simple.validator'
export const RoomCreate = props => (
    <Create title='Добавление комнаты' {...props}>
        <SimpleForm validate={validateRoom}>
            <ImageInput label="Фото" source="photo" accept="image/*" placeholder={<p>Перетащите фото сюда или нажмите.</p>}>
                <ImageField source="src" title="Фото" />
            </ImageInput>
            <ReferenceInput validate={required('Выберите категорию!')} label="Категория" source="categoryRooms.name" reference="category_rooms">
                <SelectInput optionText="name"/>
            </ReferenceInput>
        </SimpleForm>
    </Create>
);