import React from 'react'
import { 
    Create, SimpleForm, TextInput
} from 'react-admin';
import { validatePosition } from '../../validators/simple.validator'

export const PositionCreate = props => (
    <Create title="Добавление Должности" {...props}>
        <SimpleForm validate={validatePosition}>
            <TextInput label="Название" source="name"/>
        </SimpleForm>
    </Create>
);