import React from 'react';
import { 
    List, Datagrid, TextField,
 } from 'react-admin';

export const PositionList = props => (
    <List {...props} bulkActionButtons={false} title='Должности'>
        <Datagrid rowClick="edit">
            <TextField source="id"/>
            <TextField label="Название" source="name"/>
        </Datagrid>
    </List>
);