export const validateStaff = (values) => {
    const errors = {};
    if (!values.name) {
        errors.name = ['Укажите имя!'];
    }
    if (!values.lastname) {
        errors.lastname = ['Укажите фамилию!'];
    }
    if (!values.patronymic) {
        errors.patronymic = ['Укажите отчество!'];
    }
    if (!values.dateOfBirth) {
        errors.dateOfBirth = ['Укажите дату рождения!'];
    }
    if (!values.salary) {
        errors.salary= ['Укажите зарплату!'];
    }
    if (!values.position) {
        errors.position = ['Укажите должность!'];
    }
    return errors
};

export const validateReservation = (values) => {
    const errors = {};
    if (!values.check_in) {
        errors.check_in = ['Укажите дату заселения!'];
    }
    if (!values.id_room) {
        errors.id_room = ['Укажите комнату!'];
    }
    if (!values.id_staff) {
        errors.id_staff = ['Укажите сотрудника!'];
    }
    if (!values.id_client) {
        errors.id_client = ['Укажите клиента!'];
    }
    return errors
};