import React from 'react';
import { 
    List, Datagrid, TextField, DateField, 
    ReferenceField, ImageField, Show, SimpleShowLayout,
    RichTextField,
} from 'react-admin';

export const ReservationList = props => (
    <List {...props} bulkActionButtons={false} title='Регестрация'>
        <Datagrid rowClick="edit">
            <TextField source="id"/>
            <DateField label="Дата заселения" source="check_in" />
            <DateField label="Дата выселения" source="check_out" allowEmpty={true}/>
            <TextField label="Отчество" source="client.lastName" reference = "client" />
            <TextField label="id комнаты" source="room.id" reference="rooms" />
            <TextField label="Сотрудник" source="staff.lastName" reference="staff"/>
            <TextField label="Итоговая сумма" source="totalSum" allowEmpty={true} />
        </Datagrid>
    </List>
);