import React from 'react';
import { 
    List, Datagrid, TextField, 
 } from 'react-admin';

export const CategoryRoomList = props => (
    <List {...props} bulkActionButtons={false} title='Категории комнат'>
        <Datagrid rowClick="edit">
            <TextField source="id"/>
            <TextField label="Название" source="name"/>
            <TextField label="Количество комнат" source="numberOfRooms"/>
            <TextField label="Количесвто мест" source="numberOfSeats"/>
            <TextField label="Стоимость в сутки" source="cost_per_day"/>

        </Datagrid>
    </List>
);