import React from 'react'
import { 
    Edit, SimpleForm, DisabledInput, TextInput
 } from 'react-admin';
import { validatePosition } from '../../validators/simple.validator'

const PositionTitle = ({ position }) => {
    return <span>Должность {position ? `"${position.name}"` : ''}</span>;
};

export const PositionEdit = props => (
    <Edit title={<PositionTitle/>} {...props}>
        <SimpleForm validate={validatePosition}>
            <DisabledInput source="id"/>
            <TextInput label="Название" source="name"/>
        </SimpleForm>
    </Edit>
);