import React from 'react';
import { 
    List, Datagrid, TextField, DateField, 
    ReferenceField, ImageField, Show, SimpleShowLayout,
    RichTextField,
} from 'react-admin';

export const RoomList = props => (
    <List {...props} bulkActionButtons={false} title='Комнаты'>
        <Datagrid rowClick="edit" >
            <TextField source="id"/>
            <ImageField label="Фото" source="photo" />
            <ReferenceField label="Категория" source="categoryRooms.id" reference="category_rooms">
               <TextField source="name"/>
            </ReferenceField>
        </Datagrid>
    </List>
);