import React from 'react';
import { 
    Edit, SimpleForm, DisabledInput, ImageInput, 
    ReferenceInput, ImageField, SelectInput,required
} from 'react-admin';
import { validateRoom } from '../../validators/simple.validator'

export const RoomEdit = props => (
    <Edit title={"Изменение комнаты"} {...props}>
        <SimpleForm validate={validateRoom}>
            <DisabledInput source="id"/>
            <ImageInput label="Фото" source="photo" accept="image/*" placeholder={<p>Перетащите фото сюда или нажмите.</p>}>
                <ImageField source="src" title="Фото" />
            </ImageInput>
            <ReferenceInput validate={required('Выберите категорию!')} label="Категория" source="categoryRooms.id" reference="category_rooms" >
                <SelectInput optionText="name"/>
            </ReferenceInput>
        </SimpleForm>
    </Edit>
);